import BgRotator from "./components/bgRotator";

new BgRotator();

window.initMap = function initMap() {
  var uluru = { lat: 50.00329, lng: 36.302023 };
  var map = new google.maps.Map(document.getElementById("map_canvas"), {
    zoom: 17,
    center: uluru,
    mapTypeControl: false,
    streetViewControl: false,

    styles: [
      {
        featureType: "road",
        elementType: "geometry",
        stylers: [{ visibility: "simplified" }]
      },
      {
        featureType: "poi",
        elementType: "label",
        stylers: [{ visibility: "off" }]
      },
      {
        featureType: "all",
        stylers: [{ saturation: -100 }, { gamma: 0.9 }]
      }
    ]
  });
  var marker = new google.maps.Marker({
    position: uluru,
    map: map,
    title: "TE-728"
  });
  var routeCoordinates = [
    new google.maps.LatLng(50.002639, 36.301952),
    new google.maps.LatLng(50.00331, 36.301881),
    new google.maps.LatLng(50.003316, 36.302019)
  ];
  var flightPath = new google.maps.Polyline({
    path: routeCoordinates,
    geodesic: true,
    strokeColor: "#FF0000",
    strokeOpacity: 1.0,
    strokeWeight: 2
  });
};
